import { last, range } from 'lodash-es';
import moment from 'moment';

const INIT_PRICE = 10;
const STEP = 0.5;
export const lastPrice = (seriesData) => last(seriesData)?.[1] || INIT_PRICE;
export const lastTime = (seriesData) => last(seriesData)?.[0] || 0;
export const randomPrice = (seedPrice) => {
    return +((seedPrice || INIT_PRICE) + STEP * (0.52 - Math.random())).toFixed(2);
};
export const initSeriesData = (N) => {
    const data = [];
    for (const index of range(N)) {
        data.push([
            moment()
                .subtract(N - index, 'seconds')
                .valueOf(),
            randomPrice(lastPrice(data)),
        ]);
    }
    return data;
};
export const gradientColor = (from, to) => {
    return {
        type: 'linear',
        x: 0,
        y: 0,
        x2: 0,
        y2: 1,
        colorStops: [
            {
                offset: 0,
                color: from,
            },
            {
                offset: 1,
                color: to,
            },
        ],
        global: false,
    };
};