import * as am5 from '@amcharts/amcharts5';
import * as am5xy from '@amcharts/amcharts5/xy';
import { last } from 'lodash-es';
import React, { useRef, useEffect } from 'react';

import am5themes_Animated from '@amcharts/amcharts5/themes/Animated';
import { initSeriesData, randomPrice } from './helpers';

function DemoBinaryContractChart() {

    useEffect(() => {
        const root = am5.Root.new('chartdiv');
        root.setThemes([am5themes_Animated.new(root)]);
        const chart = root.container.children.push(
            am5xy.XYChart.new(root, {
                panX: true,
                panY: true,
                wheelX: 'panX',
                wheelY: 'zoomX',
                pinchZoomX: true,
            })
        );


        const xAxis = chart.xAxes.push(
            am5xy.DateAxis.new(root, {
                maxDeviation: 0.5,
                start: 0.9,
                end: 1.1,
                baseInterval: {
                    timeUnit: 'second',
                    count: 1,
                },
                renderer: am5xy.AxisRendererX.new(root, {}),
                tooltip: am5.Tooltip.new(root, {}),
            })
        );

        const yAxis = chart.yAxes.push(
            am5xy.ValueAxis.new(root, {
                min: 9.5,
                renderer: am5xy.AxisRendererY.new(root, {
                    pan: 'zoom',
                }),
            })
        );

        const series = chart.series.push(
            am5xy.LineSeries.new(root, {
                name: 'Series',
                xAxis: xAxis,
                yAxis: yAxis,
                valueYField: 'price',
                valueXField: 'time',
                tooltip: am5.Tooltip.new(root, {
                    labelText: '{valueY}',
                }),
            })
        );

        series.bullets.push(function (root, series, dataItem) {
            if (dataItem.dataContext.showBullet)
                return am5.Bullet.new(root, {
                    sprite: am5.Circle.new(root, {
                        radius: 4,
                        fill: series.get('fill'),
                    }),
                    locationX: undefined, // must set locationX to undefined to animate location x later (do not know why)
                });
        });

        /**
         * Create expiration line
         */

        const rangeDataItem = yAxis.makeDataItem({
            value: Date.now() + 10000,
        });
        xAxis.createAxisRange(rangeDataItem);
        rangeDataItem.get('grid').setAll({
            strokeWidth: 3,
            stroke: am5.color(0xf95446),
            strokeOpacity: 1,
        });

        const data = initSeriesData(5000).map((item) => ({
            time: item[0],
            price: item[1],
        }));
        last(data).showBullet = true;

        /**
         * Create last price line
         */
        const rangeLastPriceItem = yAxis.makeDataItem({
            value: last(data).price,
        });
        yAxis.createAxisRange(rangeLastPriceItem);
        rangeLastPriceItem.get('grid').setAll({
            strokeWidth: 1,
            stroke: am5.color(0x00b6f9),
            strokeOpacity: 1,
            // strokeDasharray: 10,
        });
        rangeLastPriceItem.get('label').setAll({
            fill: am5.color(0xffffff),
            text: last(data).price,
            x: am5.p100,
            background: am5.RoundedRectangle.new(root, {
                fill: am5.color(0x00b6f9),
            }),
            position: 'absolute',
        });

        /**
         * Interval push data
         */
        const intervalId = setInterval(() => {
            /**
             * Mark last data hide bullet
             * remove bullet object of last data
             */
            const lastDataItem = last(series.dataItems);
            const lastPrice = lastDataItem.dataContext.price;
            const newPrice = randomPrice(lastPrice);
            const lastNow = lastDataItem.dataContext.time;
            const newNow = Date.now();
            series.data.removeIndex(0);

            series.data.push({
                time: newNow,
                price: newPrice,
            });
            const newDataItem = last(series.dataItems);
            if (lastDataItem.bullets?.length > 0) {
                // move bullet from last to new
                newDataItem.bullets = [];
                newDataItem.bullets[0] = lastDataItem.bullets[0];
                newDataItem.dataContext.showBullet = true;
                newDataItem.bullets[0].get('sprite').dataItem = newDataItem;
                // reset bullets
                lastDataItem.dataContext.showBullet = false;
                lastDataItem.bullets = [];

                /**
                 * Animate new data
                 */
                newDataItem.animate({
                    from: lastPrice,
                    to: newPrice,
                    duration: 500,
                    key: 'valueYWorking',
                });
                newDataItem.animate({
                    from: -0.5,
                    to: 0.5,
                    duration: 500,
                    key: 'locationX',
                });
                /**
                 * Animate axis range
                 */
                rangeLastPriceItem.animate({
                    from: lastPrice,
                    to: newPrice,
                    duration: 500,
                    key: 'value',
                    y: am5.p100,
                });
                rangeLastPriceItem.get('label').setAll({
                    text: lastPrice,
                });
                //
                // newDataItem.animate({
                //     key: 'locationX',
                //     to: 0.5,
                //     from: -0.5,
                //     duration: 500,
                // });
            }
        }, 1000);

        series.data.setAll(data);

        series.strokes.template.setAll({
            strokeWidth: 2,
            stroke: am5.color(0x4393ea),
        });
        series.fills.template.setAll({
            fillOpacity: 1,
            visible: true,
            fillGradient: am5.LinearGradient.new(root, {
                stops: [
                    {
                        color: am5.color(0x4393ea),
                        opacity: 0.5,
                    },
                    {
                        color: am5.color(0xffffff),
                        opacity: 0,
                    },
                ],
                rotation: 90,
            }),
        });

        // Make stuff animate on load
        // https://www.amcharts.com/docs/v5/concepts/animations/
        series.appear(1000);
        chart.appear(1000, 100);
        return () => {
            clearInterval(intervalId);
            root.dispose();
        };
    }, []);

    return <div id='chartdiv' style={{
        width: "100vw",
        height: "100vh",
    }}></div>;
}

/**
 * chart state
 * local state
 *
 * action change chart (can depend on current chart state)
 * -- setOption chart, dispatchAction chart
 * -- setState react
 *
 * action change local ui (can depend on current chart state)
 *
 * locale ui events
 * chart events
 * api events
 */

const Login = (props) => {
    return (
        <div className='h-screen'>
            <DemoBinaryContractChart />
        </div>
    );
};

export default Login;
